@import url(https://fonts.googleapis.com/css2?family=Bitter&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bitter:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

html {
    overflow-y: scroll;
}

body {
    margin: 0;
    font-family: 'Bitter', serif;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #F8F8FF;
}

.wrapper {
    width: 60%;
    max-width: 1000px;
    width: auto;
    margin-top: 200px;
    padding: 30px 30px 10px 30px;
    border-radius: 8px;
    background-color: #B0E0E6;
}

@media (min-width: 767px) {
    .wrapper {
        margin-bottom: 50px;
    }
    .input-taskForm {
        min-width: 400px;
    }
}
@media (min-width: 992px) {
    .input-taskForm {
        min-width: 500px;
    }
}


header {
    margin-bottom: 20px;
}

header::after {
    content: '';
    display: block;
    height: 1px;
    background: black;
}

.header-title {
    padding-left: 30px;
    text-align: center;
    font-size: 18px;
}

.header-date {
    text-align: center;
    margin-bottom: 30px;
    color: #696969;
}

.addTaskForm-container {
    display: flex;
    /* align-items: center; */
    justify-content: space-between;
}


#addTaskForm {
    display: flex;
    align-items: center;
}

.input-taskForm {
    width: 70%;
    /* min-width: 300px;
    /* width: 500px; */
    /* max-width: 500px; */
    resize: vertical;
    margin-right: 10px;
    padding: 15px;
    border-radius: 10px;
    border: 0;
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.20);
    height: 20px;
}

.button-taskForm-submit {
    background-color: #87CEFA;
    padding: 5px 7px 5px 7px;
    border: 2px solid white;
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.20);
    border-radius: 5px; 
    cursor: pointer;

}

.button-taskForm-submit:active{
    background-color:#F8F8FF;
}


.button-group {
    display: flex;
    width: 201px;
    margin: 30px 0px 30px 0px;
    border: 2px solid white;
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.20);
    border-radius: 7px;
}

.not-active-button {
    background-color: #87CEFA;
    padding: 5px 5px 5px 5px;
    border: 1px solid white;
    border-radius: 5px; 
    cursor: pointer;
}

.not-active-button:hover {
    background: #B0C4DE;
}

.active-button {
    background-color:#F8F8FF;
    border: 1px solid white;
    border-radius: 5px;
    cursor: pointer;
}
.active-button:hover {
    background: #F0FFF0
}


.taskCard {
    width: 100%;
    max-width: 700px;
    height: auto;
    word-break: break-all;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 8px;
    box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.20);
    margin: 20px 40px 30px 0px;
    padding: 30px 20px 20px 20px;
    background: white;
}

.taskCards_list {
    padding: 0;
}
.taskCard-items {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.taskCard p {
    margin: 0 20px 0 0px;
}

.taskCard_completed {
    color: #A9A9A9;
    text-decoration: line-through;
    margin: 0 20px 0 0px;
}

.taskCard_important_completed {
    color: red;
    text-decoration: line-through;
    margin: 0 20px 0 0px;
}

.taskCard_important {
    color: red;
    font-weight: 600;
    margin: 0 20px 0 0px;
}

.icons-container  {
    /* opacity: 0; */
    display: flex;
    /* visibility: hidden; */
    transition:  visibility 0.5s, opacity 0.5s;
}

/* .taskCard:hover .icons-container{
    opacity: 1;
    visibility: visible;
    
} */

.icons-container img {
    margin-left: 10px;
}

.footer-about {
    text-align: center;
}

.footer-link-developer {
    text-decoration: none;    
}

.footer-link-developer:visited { 
    color: blue; 
}

img {
    cursor: pointer;
}

img:hover {
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
    transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

